.example {
    height: 200px;
    width: 300px;
    box-sizing: border-box;
    padding: 145px 0 0 95px;
    border-radius: 10px;
}
  
.btn {
    background-color: #dc3545;
    border-color: #dc3545;
    color: white;
    border: 1px solid transparent;
    box-shadow: 0 0 2px 2px rgba(225, 83, 97, 0.5);
    height: 50px;
    width: 200px;
    border-radius: 10px;
    font-size: 18px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
  }
  
  .btn:active {
    border: none;
    outline: none;
    background-color: #970d1b;
  }
  
  .btn:hover {
    background-color: #c82333;
  }
  
  .popover {
    border: 1px solid rgba(0, 0, 0, 0.2);
    width: 294px;
    height: 94px;
    position: relative;
    border-radius: 5px;
    display: none;
  }
  
  .arrow {
    bottom: -13px;
    position: absolute;
    left: 140px;
    width: 8px;
    height: 16px;
    margin: 4.8px 0;
  }
  
  .arrow::before {
    position: absolute;
    display: block;
    content: "";
    bottom: 0;
    border-width: 8px 8px 0 8px;
    border-style: solid;
    border-color: transparent;
    border-top-color: rgba(0, 0, 0, 0.25);
  }
  
  .arrow::after {
    position: absolute;
    display: block;
    content: "";
    bottom: 1px;
    border-width: 8px 8px 0 8px;
    border-style: solid;
    border-color: transparent;
    border-top-color: #fff;
  }
  
  .popover-header {
    height: 40%;
    padding: 8px;
    box-sizing: border-box;
    background-color: #f7f7f7;
    border-bottom: 1px solid #ebebeb;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    font-weight: bold;
  }
  
  .popover-body {
    padding: 9px 12px;
    background-color: #fff;
  }